<template>
    <div class="m-2">
      <el-page-header @back="" content="详情页面" class="mb-2 pb-2 border-bottom border-muted" >

</el-page-header>
      <el-row class="bg－primary" :gutter="15" >

  <el-col :span="24">
    <el-card >
      <div slot="header" >
        <h4>基本药品信息</h4>

      </div>
      <div >
             <el-form :inline="true" :model="condition" class="d-flex justify-content-center" >

          <el-form-item label="药品名称" >
                   <el-select v-model="condition.name" filterable remote  placeholder="请选择" :loading="loading" clearable
                              :remote-method="remoteFilter" >
                      <el-option
                        v-for="item in filterData"
                        :key="item.id "
                        :label="item.name"
                        :value="item.name">
                      </el-option>
                   </el-select>
          </el-form-item>

          <!--<el-form-item label="查看类型">-->
                 <!--<el-radio-group v-model="formInline.checkAll" size="mini" border @change="toogleLabel">-->
                  <!--<el-radio label="all" border class="mx-0">查看全部</el-radio>-->
                  <!--<el-radio  label="noall" border class="mx-0">按条件过滤</el-radio>-->
                   <!--</el-radio-group>-->
            <!--</el-form-item>-->
          <el-form-item>
            <el-button type="primary" @click="initData(1)" size="small" icon="el-icon-search">查 询</el-button>
          </el-form-item>
               <el-form-item>
                 <el-button type="success" size="small" @click="handleAdd">新增</el-button>
               </el-form-item>

        </el-form>

        <div class="border border-muted">

        <el-table :data="getData"  stripe border row-class-name="m-0 p-0 " header-row-class-name="text-primary m-0 p-0" size="mini"
        :highlight-current-row="flag"   height="380" header-cell-class-name="border-top border-muted p-1" cell-class-name="border border-muted p-1 " >
      <el-table-column v-for="(item,index) in title" :prop="item.prop"  :label="item.label" header-align="center" align="center" :width="item.width"
                       :key="index" :formatter="item.formatter" show-overflow-tooltip="true" >
      </el-table-column>
       <el-table-column
      align="center" width="150" label="操作">

      <template slot-scope="scope">
        <el-button
          size="mini" type="primary"
          @click="handleEdit(scope.$index,scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index,scope.row)">删除</el-button>
      </template>
    </el-table-column>
    </el-table>
          <el-pagination layout="total,prev, pager, next" background hide-on-single-page class="d-flex justify-content-end"
                               :page-size="pager.pageSize" @current-change=""
                         :current-page.sync="pager.pageNo" :total="pager.totalSize">
   </el-pagination>
          </div>
      </div>
    </el-card>

  </el-col>
</el-row>

      <el-dialog title="药品明细" :visible.sync="dialogFormVisible" >
        <el-form :model="drug" label-width="80px" size="mini" label-suffix=":" >
          <el-form-item :label="item.label" v-for="(item,index) in title" :key="index" size="mini" class="my-0 text-primary" >
            <el-input v-model="drug[item.prop]" autocomplete="off" ></el-input>
          </el-form-item>


  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="btnOk" :disabled="oriDrug==selectedRow" >修 改</el-button>
  </div>
</el-dialog>
    </div>

</template>

<script>
  import Vue from 'vue'
  import Accounting from 'accounting'
  export default {
    name: "Drug",
    data() {
      return {
        condition: {
          name: '',
        },
        pager: {
          pageNo: 1,
          pageSize: 8,
          totalSize: 0,
        },
        dataTable: [],
        dataOptions: [],
        filterData:[],
        loading: false,
        dialogFormVisible:false,
        title: [
          {prop:'id',label: '编号',width:80},
          {prop:'code',label: '编码',width:100},
          {prop:'name',label: '药品名称',width:200},
          {prop:'spci',label: '规格',width:150},
          {prop:'form',label: '剂型',width:100},
          {prop:'address',label: '厂商',width:200},
          {prop:'unit',label : '单位',width:100},
          { prop:'unitprice',label: '单价',width:100,formatter:function (row,column,cellValue,index) {
              return Accounting.formatMoney(cellValue,"￥",2,',',".")
            }},
          { prop:'searchcode',label: '搜索码',width:100},
          { prop:'cate',label: '分类',width:100}
        ],
        flag: true,
        drug:{
          id:'',
          code:'',
          name:'',
          form:'',
          address:'',
          unit:'',
          unitprice:'',
          searchcode:'',
          cate:''
        },
        oriDrug:null,
        selectedRow:null
      }
    },
    created:function() {
      this.initData(1);
    },
    methods: {
      initData(pageNo) {
        this.pager.pageNo = pageNo;
        Vue.axios.get("/polls/getDrug", {params: this.condition})
          .then((response) => {
            console.log(response)
            this.dataTable = response.data.data;
            this.pager.totalSize = response.data.count;
            if (!this.condition.name) {
              let obj = {};
              this.dataOptions = response.data.data
                .reduce((cur, next) => {
                  obj[next.name] ? "" : obj[next.name] = true && cur.push(next);
                  return cur
                }, [])
              console.log(this.dataOptions)
            }
          })
      },
      handleEdit(index, row) {
        this.selectedRow = row;
          Object.assign(this.drug,row );
           this.dialogFormVisible = true;
      },
      btnOk(){
        Vue.axios.put("/polls/getDrug",this.drug)
          .then((response) => {
            console.log(response.data);
            var self = this;
            if(response.data.updateCount > 0){
              this.dialogFormVisible = false;
              this.$message({
                message:"更新成功",
                type:"success",
                duration:1000,
                onClose(){
                  self.initData(self.pager.pageNo);
                }
              })
              // this.$alert('更新成功', '提示', {
              //   self:this,
              //  confirmButtonText: '确定',
              //   callback:function(Action) {
              //     self.initData(self.pager.pageNo);
              //   }
              // })

            }
          });
      },
      handleDelete(index, row) {
          this.$confirm('此操作将 ['+row.name+'] 删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
        });
      },
      handleAdd(){
        for (var key in this.drug){
          this.drug[key] = '';
        }
        this.dialogFormVisible = true;
      },
      remoteFilter(value) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.filterData = this.dataOptions.filter(function (val) {
            return val.searchcode.indexOf(value) >= 0
          })
        }, 200);

      },
    },
    computed: {
      getData() {
        var beginIndex = (this.pager.pageNo-1)*this.pager.pageSize;
        var endIndex = this.pager.pageNo*this.pager.pageSize;
        return this.dataTable.slice(beginIndex, endIndex);
      }
    }
  }
</script>

<style scoped>

</style>



